* {
  margin: 0;
  padding: 0;
}
.banner {
  background-image:url("../images/bg/posterNO1.png");
  color: rgb(91, 19, 90);
  background-repeat: no-repeat;
  top: 0;
  left: 0;
  padding-top: 40%;
  padding-bottom: 40%;
  text-align: center;
  font-size: 2rem;
  width: 100%;
  height: 100vh;
  object-fit: cover;
  overflow: hidden;
  font-family: "Codystar", cursive;
}

h1 {
  font-size: 3rem;
  font-family: "Press Start 2P", cursive;
  color: rgb(57, 10, 61);
}
