  color: rgb(255, 255, 255);
  padding: 6px 8px 6px 8px;
  border-radius: 3px;
  box-shadow: rgb(255, 255, 255, .6);
}
.button_div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
} 
.partners {
    margin: 0 auto;
