#connect {
  background-color: #7FCBCD;
  justify-content: center;
  text-align: center;
}

:link {
  text-decoration: none;
}

.club-details-container {
  display: flex;
  justify-content: space-evenly;
}
.club-details-container > a {
justify-content: space-evenly;
}
body {
  scroll-behavior: smooth;
}

a:hover,
a:link,
a:active,
a:any-link,
a:visited {
  text-decoration: none !important;
}

.mail,
.whatsapp,
.yt,
.fb,
.insta,
.github,
.rg {
  color: white;

  -moz-transition: 0.5s ease;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
}

.mail:hover {
  opacity: 1;
  color: var(--mail-col);
}

.whatsapp:hover {
  opacity: 1;
  color: var(--whatsapp-col);
}

.yt:hover {
  opacity: 1;
  color: var(--yt-col);
}

.fb:hover {
  opacity: 1;
  color: var(--fb-col);
}

.insta:hover {
  opacity: 1;
  color: var(--insta-col);
}

.github:hover {
  opacity: 1;
  color: rgb(23, 21, 21);
}

.rg:hover {
  opacity: 1;
  color: var(--rg-col);
}
#connect-logo {
  align-content: center;
  width: 14rem;

  margin: 2rem 0;
  opacity: 0.75;
  -moz-transition: 0.3s ease;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
}

#connect-logo:hover {
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}



#niser-link {
  color: white !important;
}

#niser-link:hover {
  color: rgba(255, 255, 255, 0.6) !important;
}