* {
  align-items: center;
  font-family:'Courier New', Courier, monospace;
}

.info {
  margin: 0 auto !important ;
  background-color: transparent;
  overflow: hidden;
  align-items: center;
  font-size: 1.5em;
  justify-content: center;
}

.info_1 {
  padding: 3vh;
  margin: 0;
  background-color: #7fcbcd;
  color: rgb(0, 0, 0);
  align-items: center;
  text-align: center;
}
.info_2 {
  padding: 3vh;
  background-image: url(../images/bg/posterNO1.png);
  background-repeat: no-repeat;
  background-size: 100%;
  background-attachment: fixed;
  background-color: #7fcbcd;
  color: rgb(0, 0, 0);
  align-items: center;
  text-align: center;
  font-family:'Courier New', Courier, monospace;
}

.img {
  width: 100vw;
  height: auto;
}

.section_heading {
  font-size: 2em ;
  padding: 0vh;
  font-family:'Courier New', Courier, monospace;
}

button {
  background-color: #3F51B5;
  color: rgb(255, 255, 255);
  padding: 6px 8px 6px 8px;
  border-radius: 3px;
  box-shadow: rgb(255, 255, 255, .6);
}
.button_div {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
} 